@charset 'utf-8';
.tui-pagination * {
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
	overflow: hidden;
	pointer-events: none;
}

.tui-pagination {
	margin-top: 50px;
	//   margin-bottom: 60px;
	line-height: normal;
	text-align: center;
	font-size: 1.1rem;
}

.tui-pagination .tui-page-btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	position: relative;
	width: 40px;
	height: 40px;
	// padding: 13px 11px;
	margin-left: 0;
	color: $darkMode-text;
	font-size: 1.1rem;
	font-weight: 500;
	font-style: normal;
	line-height: 1.333;
	text-decoration: none;
	vertical-align: middle;
	border-radius: 5px;
	margin-right: 10px;
	@media screen and (max-width: 767px) {
		margin-right: 0px;
	}
}
.tui-pagination.dark-mode .tui-page-btn {
	color: $lightMode-text;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
	color: $lightMode-text;
	background: $bg2-light;
	cursor: default;
	@media screen and (max-width: 767px) {
		background: $bg2-light;
	}
}
.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
	margin-left: 0;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
	overflow: hidden;
	height: 40px;
	padding: 0;
	font-size: 0;
	line-height: 26px;
	white-space: nowrap;
	font-weight: normal;
	// background-color: #f7f7f7;

	margin-right: 10px;
	border: 0px;
}

.tui-pagination .tui-first,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	@media screen and (max-width: 767px) {
		display: none;
	}
}

.tui-pagination .tui-last {
	margin-right: 0px;
}

.tui-pagination .tui-prev-is-ellip {
	border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
	border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
	border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
	background: url(data:image/png;base64,)
		no-repeat 0 0;

	display: inline-block;
	vertical-align: middle;
	font-size: 0px;
}

