.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
	padding: 0;
	line-height: 1;
}

.tui-pagination .tui-prev {
	margin-right: 10px;
}

.tui-pagination .tui-prev,
.tui-pagination .tui-next {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	@media screen and (max-width: 767px) {
		margin-right: 0px;
	}
}

.tui-pagination .tui-ico-first {
	width: 11px;
	height: 10px;
	background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
	width: 6px;
	height: 10px;
	background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
	width: 6px;
	height: 10px;
	background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
	width: 11px;
	height: 10px;
	background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
	width: 10px;
	height: 2px;
	margin-top: 0px;
	vertical-align: top;
	background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
	background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
	background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
	background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
	background-position: -13px -12px;
}

.tui-page-inputs {
	display: inline-block;
	vertical-align: top;
}

.tui-page-inputs .tui-page-input {
	display: inline-block;
	width: 10px;
	height: 12px;
	padding: 8px 0 6px;
	font-size: 12px;
	text-align: center;
	color: #333;
	border: 0;
	outline: none;
}

.tui-page-separator {
	display: inline-block;
	font-size: 12px;
	height: 12px;
	padding: 8px 0 6px;
	vertical-align: top;
}

// стиль кнопок с точками
.tui-page-btn .tui-next-is-ellip .tui-last-child {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	position: relative;
	width: 40px;
	height: 40px;
	// padding: 13px 11px;
	margin-left: 0;
	color: #000;
	font-size: 12px;
	font-weight: medium;
	line-height: 1.333;
	text-decoration: none;
	vertical-align: middle;
	border-radius: 5px;
	margin-right: 10px;

	@media screen and (max-width: 767px) {
		margin-right: 0px;
	}
}

.hidden-tui {
	display: none;
}
